@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading-container{position:absolute;top:0;height:100vh;background-color:#000000a8;color:white;width:100%;text-align:center;opacity:22;z-index:1;display:flex;justify-content:center;align-items:center}

.auth-container{width:390px;margin:0 auto;background:#F6F6F6;padding:20px;border-radius:10px;font-family:'Ubuntu'}.logo-container{align-items:center;text-align:center;margin-bottom:20px;font-size:40px;font-weight:bold;font-family:'Ubuntu'}.logo-container span{font-weight:100;color:orange}.logo{text-align:center}@media screen and (max-width: 500px){.auth-container{width:100% !important}}

.nav-links{
    text-decoration: none !important;
    color: rgba(0,0,0,.9);
}

.nav-links:hover{
    color: #dc3545;
}
.public-route-container {
    height: calc(100vh - 83px);
}

.configuration-route-container {
    height: calc(100vh - 83px);
}

.custom-toast {
    font-family: "Montserrat";
    border-radius: 5px;
}

.default-table {
    font-family: 'Ubuntu';
}

.default-table>thead th {
    color: #dc3545;
    vertical-align: middle;
}

.default-table>tbody td {
    vertical-align: middle;
}

.action-cell {
    width: 200px;
}

.modal-heading {
    color: #dc3545;
    font-family: 'Ubuntu';
}
.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}.App-header{background-color:white;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-header h1{color:black}.App-header img{height:50px}

